import { Card } from '@ali/4ever-plugin-card';
import { createStore }  from '@ali/4ever-utils';

interface IState {
  [key: string]: {
    isOwnerAndFirstCreated?: boolean;
  };
}

const store = createStore<IState>(Card.TYPE);

export const isOwnerAndFirstCreated = (key: string): boolean => {
  return !!store.getValue(key)?.isOwnerAndFirstCreated;
};

export const setOwnerAndFirstCreated = (key: string, bo: boolean) => {
  store.mergeValue(key, { isOwnerAndFirstCreated: bo });
};
